import React, { useEffect, useState } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import {
  Container,
  Box,
  Typography,
  Button,
  Paper,
  CircularProgress,
  Alert
} from '@mui/material';
import subscriptionService from '../services/subscriptionService';

function SubscriptionCallback({ showNotification }) {
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  
  const navigate = useNavigate();
  const location = useLocation();
  
  // Parse query parameters
  const queryParams = new URLSearchParams(location.search);
  const status = queryParams.get('status');
  const subscriptionId = queryParams.get('subscription_id');
  
  useEffect(() => {
    const handleSubscriptionCallback = async () => {
      if (status === 'success' && subscriptionId) {
        try {
          const result = await subscriptionService.verifySubscription(subscriptionId);
          
          if (result.success) {
            setSuccess('Your subscription has been successfully activated!');
            showNotification('Subscription activated successfully!', 'success');
            
            // Redirect after a short delay
            setTimeout(() => {
              navigate('/subscription', { replace: true });
            }, 3000);
          } else {
            setError('Failed to verify subscription. Please contact support.');
            showNotification('Subscription verification failed', 'error');
          }
        } catch (err) {
          setError('An error occurred while processing your subscription.');
          showNotification('Subscription processing error', 'error');
        }
      } else if (status === 'cancel') {
        setError('Subscription process was cancelled.');
        showNotification('Subscription cancelled', 'info');
        
        // Redirect after a short delay
        setTimeout(() => {
          navigate('/subscription', { replace: true });
        }, 3000);
      } else {
        setError('Invalid subscription callback.');
        showNotification('Invalid subscription parameters', 'error');
        
        // Redirect after a short delay
        setTimeout(() => {
          navigate('/subscription', { replace: true });
        }, 3000);
      }
      
      setLoading(false);
    };
    
    handleSubscriptionCallback();
  }, [status, subscriptionId, navigate, showNotification]);
  
  return (
    <Container maxWidth="md">
      <Box sx={{ my: 4 }}>
        <Typography variant="h4" component="h1" gutterBottom>
          Subscription Status
        </Typography>
        
        <Paper sx={{ p: 4 }}>
          {loading ? (
            <Box sx={{ display: 'flex', justifyContent: 'center', my: 4 }}>
              <CircularProgress />
            </Box>
          ) : (
            <Box>
              {error && <Alert severity="error" sx={{ mb: 3 }}>{error}</Alert>}
              {success && <Alert severity="success" sx={{ mb: 3 }}>{success}</Alert>}
              
              <Typography align="center" sx={{ mb: 3 }}>
                Redirecting you back to the subscription page...
              </Typography>
              
              <Box sx={{ display: 'flex', justifyContent: 'center' }}>
                <Button 
                  variant="contained" 
                  onClick={() => navigate('/subscription')}
                >
                  Go to Subscription Page
                </Button>
              </Box>
            </Box>
          )}
        </Paper>
      </Box>
    </Container>
  );
}

export default SubscriptionCallback;