import axios from 'axios';
import { API_ENDPOINTS } from '../config';

// Create axios instance
const api = axios.create();

// Add request interceptor to include token in requests
api.interceptors.request.use(
  (config) => {
    const token = localStorage.getItem('access_token');
    if (token) {
      config.headers['Authorization'] = `Bearer ${token}`;
    }
    return config;
  },
  (error) => Promise.reject(error)
);

// Authentication functions
const register = async (email, password) => {
  try {
    const response = await axios.post(API_ENDPOINTS.REGISTER, {
      email,
      password,
    });
    return { success: true, data: response.data };
  } catch (error) {
    return { 
      success: false, 
      error: error.response?.data?.detail || 'Registration failed' 
    };
  }
};

const login = async (email, password) => {
  try {
    const formData = new FormData();
    formData.append('username', email);
    formData.append('password', password);
    
    const response = await axios.post(API_ENDPOINTS.LOGIN, formData);
    
    const { access_token, token_type } = response.data;
    
    // Store token in localStorage
    localStorage.setItem('access_token', access_token);
    
    // Get user info
    const userInfo = await getUserInfo();
    localStorage.setItem('user_info', JSON.stringify(userInfo.data));
    
    return { success: true, data: userInfo.data };
  } catch (error) {
    return { 
      success: false, 
      error: error.response?.data?.detail || 'Login failed' 
    };
  }
};

const logout = () => {
  localStorage.removeItem('access_token');
  localStorage.removeItem('user_info');
  window.location.href = '/login';
};

const getUserInfo = async () => {
  try {
    const response = await api.get(API_ENDPOINTS.USER_INFO);
    return { success: true, data: response.data };
  } catch (error) {
    return { 
      success: false, 
      error: error.response?.data?.detail || 'Failed to get user info' 
    };
  }
};

const isAuthenticated = () => {
  return !!localStorage.getItem('access_token');
};

const getCurrentUser = () => {
  const userInfo = localStorage.getItem('user_info');
  return userInfo ? JSON.parse(userInfo) : null;
};

export default {
  register,
  login,
  logout,
  getUserInfo,
  isAuthenticated,
  getCurrentUser,
  api
};