import React, { useState, useEffect } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import {
  Container,
  Box,
  Typography,
  Button,
  Paper,
  Grid,
  Card,
  CardContent,
  CardActions,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Divider,
  Alert,
  CircularProgress
} from '@mui/material';
import { Check, Info } from '@mui/icons-material';
import subscriptionService from '../services/subscriptionService';
import { SUBSCRIPTION_TIERS, SUBSCRIPTION_FEATURES } from '../config';

function Subscription() {
  const [currentPlan, setCurrentPlan] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  const [pendingSubscription, setPendingSubscription] = useState(null);
  
  const location = useLocation();
  const navigate = useNavigate();
  
  // Parse query parameters
  const queryParams = new URLSearchParams(location.search);
  const status = queryParams.get('status');
  const subscriptionId = queryParams.get('subscription_id');
  
  // Fetch current subscription on component mount
  useEffect(() => {
    const fetchSubscription = async () => {
      setLoading(true);
      const result = await subscriptionService.getUserSubscription();
      
      if (result.success) {
        setCurrentPlan(result.data.plan_id || SUBSCRIPTION_TIERS.FREE);
      } else {
        setError('Failed to load subscription information');
        setCurrentPlan(SUBSCRIPTION_TIERS.FREE); // Default to free tier
      }
      
      setLoading(false);
    };
    
    fetchSubscription();
  }, []);
  
  // Handle subscription callback
  useEffect(() => {
    const handleSubscriptionCallback = async () => {
      if (status === 'success' && subscriptionId) {
        setLoading(true);
        
        // Verify the subscription
        const result = await subscriptionService.verifySubscription(subscriptionId);
        
        if (result.success) {
          setSuccess('Your subscription has been successfully activated!');
          // Refresh subscription info
          const subResult = await subscriptionService.getUserSubscription();
          if (subResult.success) {
            setCurrentPlan(subResult.data.plan_id);
          }
          
          // Clear query parameters
          navigate('/subscription', { replace: true });
        } else {
          setError('Failed to verify subscription. Please contact support.');
        }
        
        setLoading(false);
      } else if (status === 'cancel') {
        setError('Subscription process was cancelled.');
        // Clear query parameters
        navigate('/subscription', { replace: true });
      }
    };
    
    if (status) {
      handleSubscriptionCallback();
    }
  }, [status, subscriptionId, navigate]);
  
  const handleSubscribe = async (planId) => {
    setError('');
    setSuccess('');
    setLoading(true);
    
    const result = await subscriptionService.createSubscription(planId);
    
    if (result.success) {
      setPendingSubscription({
        planId,
        paypalId: result.data.subscription_id
      });
      
      // Redirect to PayPal
      window.location.href = result.data.approval_url;
    } else {
      setError('Failed to create subscription. Please try again.');
      setLoading(false);
    }
  };
  
  const handleDowngrade = async () => {
    setError('');
    setSuccess('');
    setLoading(true);
    
    const result = await subscriptionService.downgradeToFreeTier();
    
    if (result.success) {
      setSuccess('Successfully downgraded to Free tier');
      setCurrentPlan(SUBSCRIPTION_TIERS.FREE);
    } else {
      setError('Failed to downgrade subscription. Please try again.');
    }
    
    setLoading(false);
  };
  
  if (loading && !currentPlan) {
    return (
      <Container maxWidth="md">
        <Box sx={{ display: 'flex', justifyContent: 'center', my: 8 }}>
          <CircularProgress />
        </Box>
      </Container>
    );
  }
  
  return (
    <Container maxWidth="lg">
      <Box sx={{ my: 4 }}>
        <Typography variant="h4" component="h1" gutterBottom>
          Subscription Management
        </Typography>
        
        {error && <Alert severity="error" sx={{ mb: 3 }}>{error}</Alert>}
        {success && <Alert severity="success" sx={{ mb: 3 }}>{success}</Alert>}
        
        <Box sx={{ mb: 4 }}>
          <Typography variant="h6" gutterBottom>
            Current Plan: {SUBSCRIPTION_FEATURES[currentPlan]?.name || 'Free Tier'}
          </Typography>
          <Divider sx={{ mb: 2 }} />
        </Box>
        
        <Grid container spacing={3}>
          {/* Free Tier */}
          <Grid item xs={12} md={4}>
            <Card 
              elevation={currentPlan === SUBSCRIPTION_TIERS.FREE ? 6 : 1}
              sx={{ 
                height: '100%', 
                display: 'flex', 
                flexDirection: 'column',
                border: currentPlan === SUBSCRIPTION_TIERS.FREE ? '2px solid #1976d2' : 'none'
              }}
            >
              <CardContent sx={{ flexGrow: 1 }}>
                <Typography variant="h5" component="h2" gutterBottom>
                  Free Tier
                </Typography>
                <Typography variant="h6" color="text.secondary" gutterBottom>
                  $0/month
                </Typography>
                <Divider sx={{ my: 2 }} />
                <List dense>
                  {SUBSCRIPTION_FEATURES[SUBSCRIPTION_TIERS.FREE].features.map((feature, index) => (
                    <ListItem key={index}>
                      <ListItemIcon>
                        <Check color="primary" />
                      </ListItemIcon>
                      <ListItemText primary={feature} />
                    </ListItem>
                  ))}
                </List>
              </CardContent>
              <CardActions>
                {currentPlan !== SUBSCRIPTION_TIERS.FREE ? (
                  <Button 
                    fullWidth 
                    variant="outlined" 
                    onClick={handleDowngrade}
                    disabled={loading}
                  >
                    Downgrade to Free
                  </Button>
                ) : (
                  <Button 
                    fullWidth 
                    variant="contained" 
                    disabled
                  >
                    Current Plan
                  </Button>
                )}
              </CardActions>
            </Card>
          </Grid>
          
          {/* Standard Tier */}
          <Grid item xs={12} md={4}>
            <Card 
              elevation={currentPlan === SUBSCRIPTION_TIERS.STANDARD ? 6 : 1}
              sx={{ 
                height: '100%', 
                display: 'flex', 
                flexDirection: 'column',
                border: currentPlan === SUBSCRIPTION_TIERS.STANDARD ? '2px solid #1976d2' : 'none'
              }}
            >
              <CardContent sx={{ flexGrow: 1 }}>
                <Typography variant="h5" component="h2" gutterBottom>
                  Standard Tier
                </Typography>
                <Typography variant="h6" color="text.secondary" gutterBottom>
                  $9.99/month
                </Typography>
                <Divider sx={{ my: 2 }} />
                <List dense>
                  {SUBSCRIPTION_FEATURES[SUBSCRIPTION_TIERS.STANDARD].features.map((feature, index) => (
                    <ListItem key={index}>
                      <ListItemIcon>
                        <Check color="primary" />
                      </ListItemIcon>
                      <ListItemText primary={feature} />
                    </ListItem>
                  ))}
                </List>
              </CardContent>
              <CardActions>
                {currentPlan !== SUBSCRIPTION_TIERS.STANDARD ? (
                  <Button 
                    fullWidth 
                    variant="contained" 
                    onClick={() => handleSubscribe(SUBSCRIPTION_TIERS.STANDARD)}
                    disabled={loading}
                  >
                    Subscribe
                  </Button>
                ) : (
                  <Button 
                    fullWidth 
                    variant="contained" 
                    disabled
                  >
                    Current Plan
                  </Button>
                )}
              </CardActions>
            </Card>
          </Grid>
          
          {/* Premium Tier */}
          <Grid item xs={12} md={4}>
            <Card 
              elevation={currentPlan === SUBSCRIPTION_TIERS.PREMIUM ? 6 : 1}
              sx={{ 
                height: '100%', 
                display: 'flex', 
                flexDirection: 'column',
                border: currentPlan === SUBSCRIPTION_TIERS.PREMIUM ? '2px solid #1976d2' : 'none'
              }}
            >
              <CardContent sx={{ flexGrow: 1 }}>
                <Typography variant="h5" component="h2" gutterBottom>
                  Premium Tier
                </Typography>
                <Typography variant="h6" color="text.secondary" gutterBottom>
                  $19.99/month
                </Typography>
                <Divider sx={{ my: 2 }} />
                <List dense>
                  {SUBSCRIPTION_FEATURES[SUBSCRIPTION_TIERS.PREMIUM].features.map((feature, index) => (
                    <ListItem key={index}>
                      <ListItemIcon>
                        <Check color="primary" />
                      </ListItemIcon>
                      <ListItemText primary={feature} />
                    </ListItem>
                  ))}
                </List>
              </CardContent>
              <CardActions>
                {currentPlan !== SUBSCRIPTION_TIERS.PREMIUM ? (
                  <Button 
                    fullWidth 
                    variant="contained" 
                    onClick={() => handleSubscribe(SUBSCRIPTION_TIERS.PREMIUM)}
                    disabled={loading}
                  >
                    Subscribe
                  </Button>
                ) : (
                  <Button 
                    fullWidth 
                    variant="contained" 
                    disabled
                  >
                    Current Plan
                  </Button>
                )}
              </CardActions>
            </Card>
          </Grid>
        </Grid>
      </Box>
    </Container>
  );
}

export default Subscription;