import axios from 'axios';

// Get the API URL from environment or use default
const API_URL = process.env.REACT_APP_API_URL || 'http://localhost:8000';

// Create axios instance with default config
const apiClient = axios.create({
  baseURL: API_URL,
  headers: {
    'Content-Type': 'application/json',
  },
});

// Add request interceptor for authentication
apiClient.interceptors.request.use(
  (config) => {
    const token = localStorage.getItem('token');
    if (token) {
      config.headers.Authorization = `Bearer ${token}`;
    }
    return config;
  },
  (error) => Promise.reject(error)
);

// API service methods
const ApiService = {
  // Health check
  checkHealth: () => apiClient.get('/health'),
  
  // Authentication
  login: (credentials) => apiClient.post('/token', credentials),
  register: (userData) => apiClient.post('/register', userData),
  getCurrentUser: () => apiClient.get('/users/me'),
  
  // Document analysis
  analyzeDocument: (file, options = {}) => {
    const formData = new FormData();
    formData.append('file', file);
    
    Object.entries(options).forEach(([key, value]) => {
      formData.append(key, value);
    });
    
    return apiClient.post('/analyze_legal_document', formData, {
      headers: {
        'Content-Type': 'multipart/form-data',
      },
    });
  },
  
  // Video analysis
  analyzeVideo: (file, options = {}) => {
    const formData = new FormData();
    formData.append('file', file);
    
    Object.entries(options).forEach(([key, value]) => {
      formData.append(key, value);
    });
    
    return apiClient.post('/analyze_legal_video', formData, {
      headers: {
        'Content-Type': 'multipart/form-data',
      },
    });
  },
  
  // Legal chatbot
  askLegalQuestion: (question, taskId) => 
    apiClient.post(`/legal_chatbot/${taskId}`, { question }),
    
  // Subscription
  subscribe: (tier) => apiClient.post(`/subscribe/${tier}`),
};

export default ApiService;